.container__slider {
	position: relative;
	width: 100%;
	padding: 0;
	overflow: hidden;
	display: flex;
	justify-content: flex-start;
	align-items: center;
	flex-direction: row;
	flex-wrap: nowrap;
	background-color: black;
	height: 100%;
  }
  
  .container__slider > button {
	background-color: transparent;
	outline: none;
	border: 0;
	color: whitesmoke;
	font-size: 24px;
  }
  
  .container__slider > button:active {
	color: black;
  }
  
  .slider__btn-next {
	position: absolute;
	right: 0;
  }
  
  .slider__btn-prev {
	position: absolute;
	left: 0;
  }
  
  .container__slider__links {
	position: absolute;
	bottom: 5%;
	left: 50%;
	transform: translateX(-50%);
  }
  
  .container__slider__links-small {
	position: relative;
	width: 8px;
	height: 8px;
	padding: 0;
	border-radius: 50%;
	outline: none;
	border: 0;
	background-color: white;
	border: 1px solid grey;
	transition: background-color 1s ease;
	margin: 0 2px;
  }
  
  .container__slider__links-small-active {
	background-color: #00AD6F;
  }
  
  .slider__item {
	box-sizing: border-box;
	min-width: 100%;
	height: auto;
	overflow: hidden;
	object-fit: cover;
	height: auto;
	transform: translateX(-300%);
	transition: transform 1s ease;
  }
  
  .slider__item-active-1 {
	transform: translateX(0);
  }
  
  .slider__item-active-2 {
	transform: translateX(-100%);
  }
  
  .slider__item-active-3 {
	transform: translateX(-200%);
  }
  
  .slider__item-active-4 {
	transform: translateX(-300%);
  }
  
  .slider__item-active-5 {
	transform: translateX(-400%);
  }
  
  .slider__item-active-6 {
	transform: translateX(-500%);
  }
  
  .slider__item-active-7 {
	transform: translateX(-600%);
  }

  .slider__item-active-8 {
	transform: translateX(-700%);
  }

  .slider__item-active-9 {
	transform: translateX(-800%);
  }

  .slider__item-active-10 {
	transform: translateX(-900%);
  }

  .slider__item-active-11 {
	transform: translateX(-1000%);
  }

  .slider__item-active-12 {
	transform: translateX(-1100%);
  }

  .slider__item-active-13 {
	transform: translateX(-1200%);
  }

  .slider__item-active-14 {
	transform: translateX(-1300%);
  }

  .radius1{
	border-bottom-right-radius: 2.5rem;
	border-top-right-radius: 2.5rem;
  }

  .radius2{
	border-bottom-left-radius: 2.5rem;
	border-top-left-radius: 2.5rem;
  }

  
  .slider__item img {
	width: 100%;
	height: auto;
  }
  